import { Dispatch, SetStateAction } from "react"
import {
  SourceSelectorSelectedPackageInfo,
  SourceSelectorSelectedSourceInfo,
} from "../sourceSelector.types"
import { getIsSelectionSource } from "./getIsSelectionSource"

type removeContentSelectionProps = {
  selectedContentElement: SourceSelectorSelectedPackageInfo | SourceSelectorSelectedSourceInfo
  selectedSources: Array<SourceSelectorSelectedSourceInfo>
  selectedAllPackages: Array<SourceSelectorSelectedPackageInfo>
  setSelectedSources: Dispatch<SetStateAction<Array<SourceSelectorSelectedSourceInfo>>>
  setSelectedAllPackages: Dispatch<SetStateAction<Array<SourceSelectorSelectedPackageInfo>>>
}

function removeContentSelectionFunc({
  selectedContentElement,
  selectedSources,
  selectedAllPackages,
  setSelectedSources,
  setSelectedAllPackages,
}: removeContentSelectionProps): void {
  if (getIsSelectionSource(selectedContentElement)) {
    setSelectedSources(s => {
      const idx = selectedSources.findIndex(source => source.id === selectedContentElement.id)
      return [...s.slice(0, idx), ...s.slice(idx + 1)]
    })
  } else {
    setSelectedAllPackages(s => {
      const idx = selectedAllPackages.findIndex(pkg => pkg.id === selectedContentElement.id)
      return [...s.slice(0, idx), ...s.slice(idx + 1)]
    })
  }
}

export { removeContentSelectionFunc }
